let factorial=function(a){
var rez=1;	
for (var i=2;i<a+1;i++){
rez=rez*i;
}
return rez
}


export const gameFunctions = {

"factorial":function(a){
	return factorial(a);
},


"basetentoseven":function(a){
	return parseInt(a.toString(7));
},

"reorderdigits":function(a){

	return parseInt(String(a).split("").reverse().join(""));
},

"randomnumber":function(a){
	return Math.floor(Math.random()*1000);
},

"upsidedown":function(a){
var temp=String(a);

temp=temp.replace('6','x');
temp=temp.replace('9','6');
temp=temp.replace('x','9');
temp=temp.replace('7','');
temp=temp.replace('7','');
temp=temp.replace('4','');
temp=temp.replace('2','');

	return parseInt(temp);
	// 6s become 9s and 9s become 6s
}

}


