  handleKeyboardEvent(event: KeyboardEvent) {

    if (this.notes || this.block || this.creation) {
      return;
    }

    if (!isNaN(parseInt(event.key))) {
      this.btnClicked(event.key);
    }
    if (event.key == ".") {
      this.btnClicked(event.key);
    }
    if (event.key == "-") {
      //this.btnClicked(event.key);
      this.btnClicked("negate");
    }
    if (event.key == "Enter" || event.key == "=") {
      event.preventDefault();
      this.btnClicked("=");
    }
    if (
      event.key == "Backspace" ||
      event.key == "Delete" ||
      event.key == "Escape"
    ) {

      event.preventDefault();
      this.btnClicked("C");
    }

    if (event.key == "ç") {
      this.btnClicked("AC");
    }
    if (event.key == "!") {
      this.btnClicked("MR1");
    }
    if (event.key == "@") {
      this.btnClicked("MR2");
    }
    if (event.key == "#") {
      this.btnClicked("MR3");
    }
    if (event.key == "¡") {
      this.btnClicked("MS1");
    }
    if (event.key == "™") {
      this.btnClicked("MS2");
    }
    if (event.key == "£") {
      this.btnClicked("MS3");
    }

    if (event.key == "a" || event.key == "[" || event.key == "j") {
      this.btnClicked("Δ");
    }

    if (event.key == "s" || event.key == "]" || event.key == "k") {
      this.btnClicked("ψ");
    }

    if (event.key == "d" || event.key == "\\" || event.key == "l") {
      this.btnClicked("φ");
    }
    if (event.key == " ") {
      event.preventDefault();
    }
  }

