  notesButton() {
    /* debug 
this.storage.clear().then(()=>{
        window.location.reload(true);
        })
 */
    setTimeout(() => {
      if (this.filmleader) {
        return;
      }
      this.deviceFeedback.haptic(3);
      this.taptic.selection();
      if (this.slideshowstate.length > 0) {
        this.prevSlide();
        return;
      }
      this.clickSFX();

      if (this.replayscreen) {
        this.replayscreen = false;
        this.result = "";
        return;
      }

      if (this.help) {
        if (isNaN(this.level)) {
          if (this.nothingleft) {
            this.nothingleft = false;
            this.storage.clear().then(() => {
              window.location.reload(true);
            });
            return;
          }
          // skip?
          this.onlineLevelDone(false);
          this.help = false;

          return;
        }

        if (this.wideview) {
          if (this.keyboardshort) {
            this.keyboardshort = false;
            this.help = false;
            this.closeHint();
          } else {
            this.keyboardshort = true;
          }
          //alert('keyboard shortcuts');
          return;
        }
        this.about();
        return;
      }

      if (this.graph) {
        this.replay();
        return;
      }

      if (this.notes){
        this.updateNotes();
      }

      this.notes = !this.notes;
      this.help = false;
      this.graph = false;

      // NOTES focus
      /*  */
      this.focusNotes();
    }, 0);
  }

