  countIdle(bool, long: boolean = false) {
    //return; // weird......

    if (this.intervalfunc || !bool) {
      clearTimeout(this.intervalfunc);
      this.intervalfunc = null;
      return;
    } // maybe this will fix?

    if (bool) {
      var duration;

      if (long) {
        duration = Math.floor(Math.random() * 2000) + 12000;
      } else {
        duration = Math.floor(Math.random() * 1000) + 7500;
      }

      this.intervalfunc = setTimeout(() => {
        //alert(this.result);
        if (
          this.result == "" &&
          !this.graph &&
          !this.replayscreen &&
          !this.notes &&
          !this.help
        ) {
          this.NoNo(
            false,
            mockings[Math.floor(Math.random() * mockings.length)],
            false
          );
          //this.countIdle(false);
        }
      }, duration);
    }
  }

